const wrapDisplayName = (Component, hoc) => {
  if (!Component) {
    return `${hoc}()`;
  }

  if (typeof Component === 'string') {
    return `${hoc}(${Component})`;
  }

  return `${hoc}(${Component.displayName || Component.name || 'Component'})`;
};

export default wrapDisplayName;
